.main-rules{
    padding-bottom: 44px;
    position: relative;
    min-width: 462px;

    @include laptop{
        min-width: initial;
        max-width: 323px;
        padding-bottom: 0;
    }

    @include small-tablet{
        display: flex;
        grid-gap: 30px;
    }

    &__link{
        display: inline;
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: clamp(18px, 1.533vw, 23px);
        line-height: 150%;
        color: rgba(var(--cl-p),1.0);
        b{
            font-style: normal;
            font-weight: 500;
        }
        i{
            color: rgba(var(--cl-d),1.0);
            font-weight: 500;
            font-style: normal;
        }

        @include small-tablet{
            br{
                display: none;
            }
        }
    }

    &__icon{
        position: absolute;
        bottom: 0;
        right: 0;

        @include laptop{
            top: 0;
        }

        @include small-tablet{
            position: relative;
        }
        svg{
            @include laptop{
                width: 50px;
                height: 50px;
            }
            @include small-tablet{
                width: 62px;
                height: 62px;
            }
        }
       
    }
}